// Accounting Reports API Route
// GET /api/v1/accounting/reports

import { NextRequest, NextResponse } from 'next/server'
import { internalAccountingService } from '@/lib/accounting'

export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')
    const reportType = searchParams.get('type') || 'summary'
    const startDate = searchParams.get('startDate')
    const endDate = searchParams.get('endDate')

    if (!subAccountId) {
      return NextResponse.json(
        { error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    let report: any = {}

    switch (reportType) {
      case 'trial-balance':
        report = {
          type: 'trial-balance',
          title: 'Trial Balance',
          generatedAt: new Date().toISOString(),
          data: internalAccountingService.getTrialBalance(subAccountId),
        }
        break

      case 'ar-aging':
        report = {
          type: 'ar-aging',
          title: 'Accounts Receivable Aging',
          generatedAt: new Date().toISOString(),
          data: internalAccountingService.getAccountsReceivableAging(subAccountId),
        }
        break

      case 'income-statement':
        const accounts = internalAccountingService.getGLAccounts(subAccountId)
        const revenue = accounts
          .filter(a => a.accountType === 'revenue')
          .reduce((sum, a) => sum + a.balance, 0)
        const expenses = accounts
          .filter(a => a.accountType === 'expense')
          .reduce((sum, a) => sum + a.balance, 0)
        
        report = {
          type: 'income-statement',
          title: 'Income Statement',
          generatedAt: new Date().toISOString(),
          period: { startDate, endDate },
          data: {
            revenue: {
              total: revenue,
              accounts: accounts
                .filter(a => a.accountType === 'revenue' && a.balance !== 0)
                .map(a => ({ code: a.accountCode, name: a.accountName, balance: a.balance })),
            },
            expenses: {
              total: expenses,
              accounts: accounts
                .filter(a => a.accountType === 'expense' && a.balance !== 0)
                .map(a => ({ code: a.accountCode, name: a.accountName, balance: a.balance })),
            },
            netIncome: revenue - expenses,
          },
        }
        break

      case 'balance-sheet':
        const allAccounts = internalAccountingService.getGLAccounts(subAccountId)
        const assets = allAccounts
          .filter(a => a.accountType === 'asset')
          .reduce((sum, a) => sum + a.balance, 0)
        const liabilities = allAccounts
          .filter(a => a.accountType === 'liability')
          .reduce((sum, a) => sum + a.balance, 0)
        const equity = allAccounts
          .filter(a => a.accountType === 'equity')
          .reduce((sum, a) => sum + a.balance, 0)
        
        report = {
          type: 'balance-sheet',
          title: 'Balance Sheet',
          generatedAt: new Date().toISOString(),
          asOfDate: endDate || new Date().toISOString().split('T')[0],
          data: {
            assets: {
              total: assets,
              accounts: allAccounts
                .filter(a => a.accountType === 'asset' && a.balance !== 0)
                .map(a => ({ code: a.accountCode, name: a.accountName, balance: a.balance })),
            },
            liabilities: {
              total: liabilities,
              accounts: allAccounts
                .filter(a => a.accountType === 'liability' && a.balance !== 0)
                .map(a => ({ code: a.accountCode, name: a.accountName, balance: a.balance })),
            },
            equity: {
              total: equity,
              accounts: allAccounts
                .filter(a => a.accountType === 'equity' && a.balance !== 0)
                .map(a => ({ code: a.accountCode, name: a.accountName, balance: a.balance })),
            },
            totalLiabilitiesAndEquity: liabilities + equity,
            balanced: Math.abs(assets - (liabilities + equity)) < 0.01,
          },
        }
        break

      case 'summary':
      default:
        const accts = internalAccountingService.getGLAccounts(subAccountId)
        const invoices = internalAccountingService.getInvoices(subAccountId)
        const payments = internalAccountingService.getPayments(subAccountId)
        const customers = internalAccountingService.getCustomers(subAccountId)
        const journalEntries = internalAccountingService.getJournalEntries(subAccountId)
        const arAging = internalAccountingService.getAccountsReceivableAging(subAccountId)

        report = {
          type: 'summary',
          title: 'Accounting Summary',
          generatedAt: new Date().toISOString(),
          data: {
            overview: {
              totalAssets: accts.filter(a => a.accountType === 'asset').reduce((s, a) => s + a.balance, 0),
              totalLiabilities: accts.filter(a => a.accountType === 'liability').reduce((s, a) => s + a.balance, 0),
              totalEquity: accts.filter(a => a.accountType === 'equity').reduce((s, a) => s + a.balance, 0),
              totalRevenue: accts.filter(a => a.accountType === 'revenue').reduce((s, a) => s + a.balance, 0),
              totalExpenses: accts.filter(a => a.accountType === 'expense').reduce((s, a) => s + a.balance, 0),
            },
            counts: {
              glAccounts: accts.length,
              invoices: invoices.length,
              payments: payments.length,
              customers: customers.length,
              journalEntries: journalEntries.length,
            },
            invoices: {
              total: invoices.length,
              draft: invoices.filter(i => i.status === 'draft').length,
              sent: invoices.filter(i => i.status === 'sent').length,
              paid: invoices.filter(i => i.status === 'paid').length,
              overdue: invoices.filter(i => i.status === 'overdue').length,
              totalAmount: invoices.reduce((s, i) => s + i.total, 0),
              totalPaid: invoices.reduce((s, i) => s + i.amountPaid, 0),
              totalOutstanding: invoices.reduce((s, i) => s + i.amountDue, 0),
            },
            payments: {
              total: payments.length,
              completed: payments.filter(p => p.status === 'completed').length,
              pending: payments.filter(p => p.status === 'pending').length,
              failed: payments.filter(p => p.status === 'failed').length,
              totalAmount: payments.filter(p => p.status === 'completed').reduce((s, p) => s + p.amount, 0),
            },
            arAging,
          },
        }
        break
    }

    return NextResponse.json({
      success: true,
      data: report,
    })
  } catch (error: any) {
    console.error('Generate report error:', error)
    return NextResponse.json(
      { error: error.message || 'Failed to generate report' },
      { status: 500 }
    )
  }
}
